.main {
  background-color: #ffffff;
  min-height: 60vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  color: #000000;
  padding: 15px;
}

.form {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  height: min-content;
  padding: 10px 10px;
  background-color: #ffffff;

  border: 1px solid #ccc;
  box-shadow:0 4px 5px rgba(0, 0, 0, .14);
  margin: 5px 0 0 0;
}

.right, .left {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.button {
  font-weight: 700;
  color: #fff;
  background-color: #f11e22;
  text-transform: uppercase;
  width: 30vw;
  padding: 10px ;
  margin: 10px 0 10px 0;
}

.button:hover,
.button:focus,
.button:active{
  color: #f2f2f2;
  box-shadow:0 4px 5px rgba(0, 0, 0, .14);
}
.title {
  text-transform: uppercase;
  color: #f11e22;
  font-size: 3vw;
}

.description {
 text-align: justify;
}

.left {
  width: 40%;
}

.right {
  width: 50%;
}

.left img {
  width: 85%;
}

.comicsList {
  display: block;
  list-style: none;
  counter-reset: list;
  margin: 0;
  padding: 0;
}
.comicsList li {
  position: relative;
  display: block;
  height: 2rem;
  line-height: 2rem;
  margin-left: 1.75rem;
  margin-bottom: .25rem;
  padding-left: 1rem;
  padding-right: .5rem;
  color: #000;
  background: #fff;
  /*white-space: nowrap;*/
  border-radius: .25rem;
}
.comicsList li:last-child {
  margin-bottom: 0;
}
.comicsList li::before {
  content: counter(list);
  counter-increment: list;
  position: absolute;
  left: -1.5rem;
  top: -.25rem;
  bottom: -.25rem;
  width: 2rem;
  line-height: 2rem;
  border-radius: 1.5rem;
  border: .25rem solid #fff;
  text-align: center;
  color: #fff;
  background: #f11e22;
}

.ellipsis {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
}
.ellipsis div {
  position: absolute;
  top: 27px;
  width: 11px;
  height: 11px;
  background: #f11e22;
  animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.ellipsis div:nth-child(1) {
  left: 6px;
  animation: ellipsis1 0.6s infinite;
}
.ellipsis div:nth-child(2) {
  left: 6px;
  animation: ellipsis2 0.6s infinite;
}
.ellipsis div:nth-child(3) {
  left: 26px;
  animation: ellipsis2 0.6s infinite;
}
.ellipsis div:nth-child(4) {
  left: 45px;
  animation: ellipsis3 0.6s infinite;
}
@keyframes ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}
@keyframes ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}
@keyframes ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(19px, 0);
  }
}

@media only screen and (max-width: 480px) {
  .heroTitle {
    font-size: 3vw;
  }
  .title {
    font-size: 4vw;
  }
  .button {
    font-size: 2.3vw;
  }
  .right {
    font-size: 3vw;
  }
  .comicsList li {
    font-weight: 700;
    font-size: 2.5vw;
  }
  .form {
    box-shadow: none;
  }
  .right, .left, .form{
    display: block;
    width: 100%;
  }
  .button {
    width: 80vw;
  }
}

@media only screen and (min-width: 480px) {
  .heroTitle {
    font-size: 2vw;
  }
  .title {
    font-size: 2vw;
  }
  .button {
    font-size: 2vw;
  }
  .right {
    font-size: 2vw;
  }
  .comicsList {
    font-size: 2vw;
  }
}

@media only screen and (min-width : 768px) {
  .heroTitle {
    font-size: 1.4vw;
  }
  .title {
    font-size: 1.4vw;
  }
  .button {
    font-size: 1vw;
  }
  .right {
    font-size: 1.4vw;
  }
  .comicsList {
    font-size: 1.2vw;
  }
}

@media only screen and (min-width: 992px) {
  .heroTitle {
    font-size: 1vw;
  }
  .title {
    font-size: 1vw;
  }
  .button {
    font-size: 0.7vw;
  }
  .right {
    font-size: 1vw;
  }
  .comicsList {
    font-size: 0.8vw;
  }
  .left img {
    width: 40%;
  }
}
