.main {
    font-family: Roboto, sans-serif;
    height: 100vh;
    background-color: white;
    color: #f11e22;
    font-size: 72px;
    overflow: hidden;
    display: flex;
    flex-direction: column;
}

.text {
    padding: 0;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    font-weight: 500;
    color: #f11e22;
}

.link {
  display: flex;
  justify-content: center;
  text-decoration: none;
}

.button {
  padding: 10px;
  margin: 0;
  font-weight: 700;
  color: #fff;
  background-color: #f11e22;
  text-transform: uppercase;
  width: 200px;
  font-size: 14px;
}

.button:hover,
.button:focus,
.button:active{
    color: #f2f2f2;
    box-shadow:0 4px 5px rgba(0, 0, 0, .14);
}
